@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 600;
}

:root {
  --placeholder-color: #3d4e61;
}

* {
  box-sizing: border-box;
  padding: 0;
  font-family: Avenir, Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 22px;
}

.form {
  width: 100%;
  margin-top: 20px;
  display: flex;
}

.form__searchbar {
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  height: 70px;
  padding: 0 62px;
  transition: all .3s;
  position: relative;
  box-shadow: 0 1px 8px #3d4e611a;
}

.form__searchbar--small {
  height: 42px;
  padding: 0 33px;
}

.form__searchbar:after {
  content: "";
  background: url("Search.ccd3c07b.svg") center / contain no-repeat;
  width: 19px;
  height: 19px;
  display: block;
  position: absolute;
  top: 25px;
  left: 26px;
}

.form__searchbar--small:after {
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.form__input {
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
}

.form__input--small {
  font-size: 14px;
}

.form__input::placeholder {
  color: var(--placeholder-color);
}

.form__searchbar:hover {
  box-shadow: 0 4px 4px #00000040;
}

.form__input:focus {
  text-shadow: 2px 2px 4px #0000003d;
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  font-weight: 600;
}

.form__searchbar:focus-within {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
}

